/* global navigator */
import { exist } from '../utils';

const copyCurrentLink = () => {
	const SELECTORS = {
		link: '.js-copy-current-url',
	};

	const ATTRIBUTES = {
		copied: 'data-copied',
	};

	const ACTIVE_DELAY = 3000;

	const copyCurrentUrl = (e, $link) => {
		e.preventDefault();
		const currentUrl = window.location.href;

		if (!navigator?.clipboard) return;

		navigator.clipboard
			.writeText(currentUrl)
			.then(() => {
				$link.setAttribute(ATTRIBUTES.copied, 'true');

				setTimeout(() => {
					$link.removeAttribute(ATTRIBUTES.copied);
				}, ACTIVE_DELAY);
			})
			.catch((err) => {
				console.log('Something went wrong', err);
			});
	};

	const $links = document.querySelectorAll(SELECTORS.link);
	if (!exist($links)) return;

	$links.forEach(($link) => {
		$link.addEventListener('click', (e) => copyCurrentUrl(e, $link));
	});
};

export default copyCurrentLink;
