import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import { exist } from '../../utils';

gsap.registerPlugin(ScrollTrigger);

const SELECTORS = {
	section: '.js-product',
	cardParent: '.js-product-cards',
	line: '.js-product-line',
	circle: '.js-product-circle',
};

const CLASSNAMES = {
	activeCard: 'hero__card--active_mod',
};

const floatingCards = ($card, tl) => {
	const { yPercent, xPercent } = $card.dataset;
	const coef = 1.5;

	tl.fromTo(
		$card,
		{
			yPercent: 0,
			xPercent: 0,
		},
		{
			yPercent: yPercent * coef,
			xPercent: xPercent * coef,
			duration: 2.5,
			repeat: -1,
			ease: 'none',
			yoyo: true,
		},
		'floating',
	);
};

const lineRotating = ($line, tl) => {
	tl.fromTo(
		$line,
		{
			rotate: 0,
		},
		{
			rotate: 360,
			duration: 3,
			repeat: -1,
			ease: 'none',
		},
	);
};

const circleAnimation = ($circle) => {
	gsap.fromTo(
		$circle,
		{
			scale: 1,
			opacity: 1,
		},
		{
			duration: 2,
			scale: 2,
			opacity: 0,
		},
	);
};

const diactiveCard = ($card) => {
	$card?.classList.remove(CLASSNAMES.activeCard);
};

const setActiveCard = ($card, $circle) => {
	$card.classList.add(CLASSNAMES.activeCard);

	if ($circle) {
		circleAnimation($circle);
	}
};

const productCircle = () => {
	const $sections = document.querySelectorAll(SELECTORS.section);
	if (!exist($sections)) return;

	$sections.forEach(($section) => {
		const $cardParent = $section.querySelector(SELECTORS.cardParent);
		if (!exist($cardParent)) return;

		const { autoplayDelay, initialDelay } = $cardParent.dataset;

		const $line = $section.querySelector(SELECTORS.line);
		const $circle = $section.querySelector(SELECTORS.circle);
		const $cards = Array.from($cardParent.children);

		const lineTl = gsap.timeline({
			paused: true,
		});

		const floatingTl = gsap.timeline({
			paused: true,
		});

		if (exist($line)) {
			lineRotating($line, lineTl);
		}

		if (exist($cards)) {
			const delay = parseInt(autoplayDelay) || 4000;
			const firstDelay = parseInt(initialDelay) || 2000;

			let currentCardIndex = 0;
			let autoplayInterval;

			const $activeCards = $cards.filter(($card) => !$card.dataset.inactive);
			const $sortedCards = $activeCards.sort((a, b) => {
				return parseInt(a.dataset.autoplayOrder || 0) - parseInt(b.dataset.autoplayOrder || 0);
			});

			const startAutoplay = () => {
				autoplayInterval = setInterval(() => {
					const nextCardIndex = (currentCardIndex + 1) % $sortedCards.length;

					diactiveCard($sortedCards[currentCardIndex]);
					setActiveCard($sortedCards[nextCardIndex], $circle);

					currentCardIndex = nextCardIndex;
				}, delay);
			};

			const stopAutoplay = () => {
				const $activeCard = document.querySelector(`.${CLASSNAMES.activeCard}`);
				if ($activeCard) diactiveCard($activeCard);

				clearInterval(autoplayInterval);
			};

			$cards.forEach(($card) => {
				floatingCards($card, floatingTl);
			});

			$sortedCards.forEach(($card, index) => {
				$card.addEventListener('mouseenter', () => {
					stopAutoplay();
					setActiveCard($card, $circle);
				});
				$card.addEventListener('mouseleave', () => {
					diactiveCard($card);
					currentCardIndex = index;
					startAutoplay();
				});
			});

			ScrollTrigger.create({
				trigger: $section,
				onEnter: () => {
					setTimeout(() => {
						setActiveCard($sortedCards[currentCardIndex], $circle);
						startAutoplay();
					}, firstDelay);
				},
				onEnterBack: () => {
					startAutoplay();
				},
				onLeave: () => {
					stopAutoplay();
				},
				onLeaveBack: () => {
					stopAutoplay();
				},
			});
		}

		const stopAnimation = () => {
			lineTl.pause();
			floatingTl.pause();
		};

		const playAnimation = () => {
			lineTl.play();
			floatingTl.play();
		};

		ScrollTrigger.create({
			trigger: $section,
			onEnter: () => {
				playAnimation();
			},
			onEnterBack: () => {
				playAnimation();
			},
			onLeave: () => {
				stopAnimation();
			},
			onLeaveBack: () => {
				stopAnimation();
			},
		});
	});
};

export default productCircle;
