// ------------------- imports
import { onWindowResize } from 'utils';
// ------------------- imports###

// ------------------  import components
import faq from 'components/faq';
import header from 'components/header';
import productCircle from 'components/animations/product-circle';
import testimonials from 'components/testimonials';
import copyCurrentLink from 'components/copy-current-link';
import article from 'components/article';
import timer from 'components/timer';
import stories from 'components/stories';
import calculator from 'components/calculator';
import progressBar from 'components/progress-bar';
import filters from '../components/filters';
import filtersMarkup from '../components/filtersMarkup';
import contactSelect from '../components/contact-select';
import { calcViewportHeight } from '../utils';
import achorScrolling from '../components/anchorScrolling';
import timerBanner from '../components/timer-banner';
import shopifyBanner from '../components/shopify-banner';
import { videoViewportController } from '../components/video-viewport-controller';
// ------------------  import components###

const layout = () => {
	onWindowResize(() => {
		calcViewportHeight();
	});
	calcViewportHeight();
	header();
	productCircle();
	faq();
	testimonials();
	timer();
	copyCurrentLink();
	stories();
	calculator();
	article();
	progressBar();
	filters();
	filtersMarkup();
	contactSelect();
	achorScrolling();
	timerBanner();
	shopifyBanner();
	videoViewportController('.js-video-element');
};

export default layout;
