import { exist } from '../utils/index';

const contactSelect = () => {
	const SELECTORS = {
		form: '.js-contact-form',
		selectBtn: '.js-subject-select-btn',
		selectBtnText: '.js-subject-select-btn-text',
		selectList: '.js-subject-select-list',
		subjectRadioBtn: '.js-subject-radio',
		formRadioBlock: '.js-radio-block',
	};

	const CLASSNAMES = {
		selectsOpenState: 'contact_form__select_list--open_state',
		selectBtnCheckedState: 'contact_form__select_btn--checked_state',
		selectBtnActiveState: 'contact_form__select_btn--active_state',
		selectOptionActiveState: 'form_radio__block--active_state',
	};

	const $forms = document.querySelectorAll(SELECTORS.form);
	if (!exist($forms)) return;

	$forms.forEach(($form) => {
		const $formEl = $form.querySelector('form');
		const $selectBtn = $form.querySelector(SELECTORS.selectBtn);
		const $selectBtnText = $form.querySelector(SELECTORS.selectBtnText);
		const $selectList = $form.querySelector(SELECTORS.selectList);
		const $subjectRadioBtns = $form.querySelectorAll(SELECTORS.subjectRadioBtn);
		const $subjectRadioBlocks = $form.querySelectorAll(SELECTORS.formRadioBlock);

		if (
			!exist($formEl) ||
			!exist($selectBtn) ||
			!exist($selectBtnText) ||
			!exist($selectList) ||
			!exist($subjectRadioBtns) ||
			!exist($subjectRadioBlocks)
		)
			return;

		const closeSelect = () => {
			$selectList.classList.remove(CLASSNAMES.selectsOpenState);
			$selectBtn.classList.remove(CLASSNAMES.selectBtnActiveState);
		};

		const onOutsideClick = (e) => {
			const isInsideSelect = $selectList.contains(e.target);

			if (!isInsideSelect) {
				closeSelect();
			}
		};

		const clearAllSelects = () => {
			$subjectRadioBlocks.forEach(($subjectRadioBlock) => {
				$subjectRadioBlock.classList.remove(CLASSNAMES.selectOptionActiveState);
			});

			$selectBtnText.textContent = 'Subject';
		};

		const toggleSelect = (e) => {
			e.stopPropagation();

			const isOpen = $selectList.classList.contains(CLASSNAMES.selectsOpenState);

			if (!isOpen) {
				$selectList.classList.add(CLASSNAMES.selectsOpenState);
				$selectBtn.classList.add(CLASSNAMES.selectBtnActiveState);
				window.addEventListener('click', onOutsideClick);
			} else {
				$selectList.classList.remove(CLASSNAMES.selectsOpenState);
				$selectBtn.classList.remove(CLASSNAMES.selectBtnActiveState);
				window.removeEventListener('click', onOutsideClick);
			}
		};

		$selectBtn.addEventListener('click', toggleSelect);
		$subjectRadioBtns.forEach(($subjectRadioBtn) => {
			$subjectRadioBtn.addEventListener('change', (e) => {
				clearAllSelects();

				const $closestRadioBlock = e.currentTarget.closest(SELECTORS.formRadioBlock);
				$selectBtnText.textContent = $closestRadioBlock.dataset.value;
				$selectBtn.classList.add(CLASSNAMES.selectBtnCheckedState);
				$closestRadioBlock.classList.add(CLASSNAMES.selectOptionActiveState);
				closeSelect();
			});
		});
		$formEl.addEventListener('submit', clearAllSelects);
	});
};

export default contactSelect;
