import { exist, onWindowResize } from '../utils/index';

const stories = () => {
	const SELECTORS = {
		wrapper: '.js-stories',
		storiesParentItem: '.js-stories-parent',
		storiesText: '.js-stories-text',
		storiesBtn: '.js-stories-btn',
	};

	const CLASSNAMES = {
		itemInitState: 'stories_card__top--init_state',
		itemOpenState: 'stories_card__top--open_state',
	};

	const $sections = document.querySelectorAll(SELECTORS.wrapper);

	if (!exist($sections)) return;

	$sections.forEach(($section) => {
		const $storiesItems = $section.querySelectorAll(SELECTORS.storiesParentItem);

		if (!exist($storiesItems)) return;

		$storiesItems.forEach(($storiesItem) => {
			if (exist($storiesItem)) {
				const $item = $storiesItem;
				const $storiesText = $storiesItem.querySelector(SELECTORS.storiesText);
				const $storiesBtn = $storiesItem.querySelector(SELECTORS.storiesBtn);

				if (!exist($storiesText)) return;

				const NUM_ROWS = 4;
				let htmlFontSize;
				let elBaseHeight;
				let elScrollHeight = $storiesText.scrollHeight;
				let transitionDuration;
				let isTextOpen = false;

				const setHeight = (height) => {
					$storiesText.style.maxHeight = `${height / htmlFontSize}rem`;
				};

				const openText = () => {
					isTextOpen = true;
					$item.classList.add(CLASSNAMES.itemOpenState);
					setHeight(elScrollHeight);
				};

				const closeText = () => {
					isTextOpen = false;
					setHeight(elBaseHeight);
					setTimeout(() => {
						$item.classList.remove(CLASSNAMES.itemOpenState);
					}, transitionDuration);
				};

				const onBtnClick = () => {
					if (isTextOpen) {
						closeText();
					} else {
						openText();
					}
				};

				const init = () => {
					htmlFontSize = parseFloat(window.getComputedStyle(document.documentElement).fontSize);

					const textHeight = $storiesText.scrollHeight;
					const textStyles = window.getComputedStyle($storiesText);
					const textLineHeight = parseFloat(textStyles.lineHeight);
					const textLineHeightOfOneLine = (textLineHeight * htmlFontSize) / htmlFontSize;

					elBaseHeight = Math.round(textLineHeightOfOneLine * NUM_ROWS);

					if (textHeight > elBaseHeight) {
						$storiesItem.classList.add(CLASSNAMES.itemInitState);
					} else {
						$storiesItem.classList.remove(CLASSNAMES.itemOpenState, CLASSNAMES.itemInitState);
					}

					closeText();
					transitionDuration = parseFloat(textStyles.transitionDuration) * 1000 || 500;
					elScrollHeight = textHeight;
				};

				if (exist($storiesBtn)) {
					$storiesBtn.addEventListener('click', onBtnClick);
				}

				onWindowResize(() => {
					init();
				});
			}
		});
	});
};

export default stories;
