import { exist } from '../utils/index';

const calculator = () => {
	const SELECTORS = {
		wrapper: '.js-calculator-wrapper',
		input: '.js-calculator-input',
		result: '.js-calculator-result',
	};

	const $wrappers = document.querySelectorAll(SELECTORS.wrapper);

	if (!exist($wrappers)) return;

	const changeInputValue = (e, coef, $calculatorResult) => {
		const target = e.target || e;
		const maxSymbolsValue = parseInt(target.getAttribute('maxlength')) || 5;
		const $res = $calculatorResult;
		if (target.value.length > maxSymbolsValue) {
			target.value = target.value.slice(0, maxSymbolsValue);
		}

		target.value = target.value.replace(/[^\d]/g, '');

		let result = target.value * coef;

		if (result >= 1000) {
			result = result.toLocaleString('en-US', {
				minimumFractionDigits: 2,
				maximumFractionDigits: 2,
			});
		} else {
			result = result.toFixed(2);
		}

		$res.innerText = result;
	};

	$wrappers.forEach(($wrapper) => {
		const $calculatorInput = $wrapper.querySelector(SELECTORS.input);
		const $calculatorResult = $wrapper.querySelector(SELECTORS.result);

		if (exist($calculatorInput) && exist($calculatorResult)) {
			const coef = parseFloat($wrapper.dataset.calculatorCoef) || 0;

			$calculatorInput.addEventListener('input', (e) => changeInputValue(e, coef, $calculatorResult));
			changeInputValue($calculatorInput, coef, $calculatorResult);
		}
	});
};

export default calculator;
