import { exist } from '../utils/index';

const shopifyBanner = () => {
	const SELECTORS = {
		banner: '.js-shopify-banner',
		closeTrigger: '.js-close-shopify-banner',
	};

	const $closeBtn = document.querySelector(SELECTORS.closeTrigger);
	const $banner = document.querySelector(SELECTORS.banner);

	if (!exist($banner)) return;

	$closeBtn.addEventListener('click', () => {
		$banner.remove();
		document.body.classList.remove('body--shopify_banner_mod');
	});
};

export default shopifyBanner;
