import gsap from 'gsap';
import ScrollToPlugin from 'gsap/dist/ScrollToPlugin';
import { exist } from '../utils';

/** Anchor scrolling to the section */
const achorScrolling = () => {
	const SELECTORS = {
		anchor: 'a',
		header: '.js-header',
	};
	const $links = document.querySelectorAll(SELECTORS.anchor);
	const $header = document.querySelector(SELECTORS.header);

	if (!exist($links)) return;

	gsap.registerPlugin(ScrollToPlugin);

	$links.forEach(($link) => {
		$link.addEventListener('click', (e) => {
			const url = $link.getAttribute('href');

			if (!url) return null;

			const urlArray = url.split('#');
			if (!urlArray.length || !url.includes('#')) return null;

			const hash = urlArray.pop();

			const $section = document.querySelector(`[data-id="${hash}"]`);

			if (!exist($section)) return null;

			e.preventDefault();

			const headerHeight = $header?.clientHeight || 0;
			const offset = 20;
			const scrollTop = $section.getBoundingClientRect().top + window.scrollY;

			gsap.to(window, {
				scrollTo: {
					y: scrollTop,
					offsetY: headerHeight + offset,
				},
				duration: 1,
				ease: 'ease',
			});

			return null;
		});
	});
};

export default achorScrolling;
