import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import ScrollToPlugin from 'gsap/dist/ScrollToPlugin';
import { Fancybox } from '@fancyapps/ui';
import { BREAKPOINTS, exist } from '../utils';
import '@fancyapps/ui/dist/fancybox/fancybox.css';

gsap.registerPlugin(ScrollTrigger, ScrollToPlugin);

const SELECTORS = {
	header: '.js-header',
	section: '.js-article',
	anchor: '.js-article-anchor',
	aside: '.js-article-aside',
	nav: '.js-article-list',
	anchorRef: '.wp-block-heading[id]',
};

const CLASSNAMES = {
	activeLink: 'article__link--active_state',
	li: 'article__item',
	link: 'article__link',
};

const initPin = ($parent, $aside, offset) => {
	ScrollTrigger.create({
		trigger: $parent,
		start: `top top+=${offset}`,
		end: 'bottom bottom',
		pin: $aside,
		pinSpacing: false,
		pinType: 'fixed',
	});
};

const scrollTo = (id, offset) => {
	const $section = document.querySelector(`#${id}`);
	if (!exist($section)) return;

	gsap.to(window, {
		scrollTo: {
			y: $section,
			offsetY: offset,
			autoKill: false,
		},
		duration: 1,
		ease: 'ease',
	});
};

const anchorClick = ($link, offset) => {
	const sectionId = $link.getAttribute('href').replace(/#/g, '');
	scrollTo(sectionId, offset);
};

const createNavigation = ($nav, $refs, offset) => {
	$refs.forEach((item) => {
		const $li = document.createElement('li');
		$li.classList.add(CLASSNAMES.li);

		const $a = document.createElement('a');
		$a.classList.add(CLASSNAMES.link);
		$a.href = `#${item.id}`;
		$a.textContent = item.textContent;

		$li.appendChild($a);
		$nav.appendChild($li);

		$a.addEventListener('click', (e) => {
			e.preventDefault();
			anchorClick($a, offset);
		});
	});
};

const changeActiveLink = ($ref) => {
	const { id } = $ref;
	const $newActiveLink = document.querySelector(`[href='#${id}']`);

	if (!exist($newActiveLink)) return;

	const $prevActiveLink = document.querySelector(`.${CLASSNAMES.activeLink}`);
	$prevActiveLink?.classList.remove(CLASSNAMES.activeLink);

	$newActiveLink.classList.add(CLASSNAMES.activeLink);
};

const initAnchorHighlight = ($ref, offset) => {
	ScrollTrigger.create({
		trigger: $ref,
		start: `top-=${$ref.clientHeight + offset} top`,
		onEnter: () => changeActiveLink($ref),
		onEnterBack: () => changeActiveLink($ref),
	});
};

const initFancy = () => {
	Fancybox.bind('[data-fancybox]', {
		contentClick: 'close',
		caption: (fancybox, slide) => {
			const { caption, src } = slide;

			return `${caption || ''} <a class="fancybox_link" href="${src}" target="_blank">View full size</a>`;
		},
	});
};

const article = () => {
	const mm = gsap.matchMedia();

	mm.add(`(min-width: ${BREAKPOINTS.mediaPoint1}px)`, () => {
		initFancy();
	});

	mm.add(`(max-width: ${BREAKPOINTS.mediaPoint1 - 0.5}px)`, () => {
		Fancybox.destroy();
	});

	const $sections = document.querySelectorAll(SELECTORS.section);
	if (!exist($sections)) return;

	const $header = document.querySelector(SELECTORS.header);
	const additionalOffset = 28;
	const offset = ($header.offsetHeight || 0) + additionalOffset;

	if (window.innerWidth < BREAKPOINTS.mediaPoint1) return;

	$sections.forEach(($parent) => {
		const $asides = Array.from($parent.querySelectorAll(SELECTORS.aside));
		const $anhorRefs = Array.from($parent.querySelectorAll(SELECTORS.anchorRef));
		const $nav = $parent.querySelector(SELECTORS.nav);

		if (!exist($anhorRefs) || !exist($nav)) return;

		createNavigation($nav, $anhorRefs, offset);

		ScrollTrigger.refresh();

		$anhorRefs.forEach(($ref) => {
			initAnchorHighlight($ref, offset);
		});

		if (exist($asides)) {
			$asides.forEach(($element) => {
				initPin($parent, $element, $header.offsetHeight || 0);
			});
		}
	});
};
export default article;
