import { exist } from '../utils/index';

const filters = () => {
	const SELECTORS = {
		filters: '.js-filters-wrap',
		filtersSelects: '.js-filters-selects',
		categoryBtn: '.js-category-filter-btn',
		showingBtn: '.js-showing-filter-btn',
		categoryList: '.js-category-filter-list',
		showingList: '.js-showing-filter-list',
		filterWrapper: '.js-filter-wrapper',
		filtersToggler: '.js-filters-toggler',
		filtersTogglerText: '.js-filters-toggler-text',
	};

	const CLASSNAMES = {
		filtersSelectsOpenState: 'filters__selects--open_state',
		filterListOpenState: 'filters__select_list--open_state',
	};

	const $filters = document.querySelectorAll(SELECTORS.filters);
	if (!exist($filters)) return;

	$filters.forEach(($filter) => {
		const $categoryBtn = $filter.querySelector(SELECTORS.categoryBtn);
		const $showingBtn = $filter.querySelector(SELECTORS.showingBtn);
		const $categoryList = $filter.querySelector(SELECTORS.categoryList);
		const $showingList = $filter.querySelector(SELECTORS.showingList);
		const $filtersSelects = $filter.querySelector(SELECTORS.filtersSelects);
		const $filtersToggler = $filter.querySelector(SELECTORS.filtersToggler);
		const $filtersTogglerText = $filter.querySelector(SELECTORS.filtersTogglerText);

		if (
			!exist($categoryBtn) ||
			!exist($showingBtn) ||
			!exist($categoryList) ||
			!exist($showingList) ||
			!exist($filtersSelects) ||
			!exist($filtersToggler) ||
			!exist($filtersTogglerText)
		)
			return;

		let $activeList = null;

		const closeAllFilters = () => {
			$categoryList.classList.remove(CLASSNAMES.filterListOpenState);
			$showingList.classList.remove(CLASSNAMES.filterListOpenState);
		};

		const onOutsideClick = (e) => {
			const isInsideFilter = $showingList.contains(e.target) || $categoryList.contains(e.target);

			if (!isInsideFilter) {
				closeAllFilters();
			}
		};

		const toggleFilter = (e, $list) => {
			e.stopPropagation();

			if ($activeList && $activeList !== $list) {
				$activeList.classList.remove(CLASSNAMES.filterListOpenState);
			}

			const isOpen = $list.classList.contains(CLASSNAMES.filterListOpenState);

			if (!isOpen) {
				$list.classList.add(CLASSNAMES.filterListOpenState);
				document.addEventListener('click', onOutsideClick);
				$activeList = $list;
			} else {
				$list.classList.remove(CLASSNAMES.filterListOpenState);
				document.removeEventListener('click', onOutsideClick);
				$activeList = null;
			}
		};

		const toggleSelects = () => {
			const isOpen = $filtersSelects.classList.contains(CLASSNAMES.filtersSelectsOpenState);

			if (!isOpen) {
				$filtersSelects.classList.add(CLASSNAMES.filtersSelectsOpenState);
				const togglerText = $filtersToggler.dataset.openState;
				$filtersTogglerText.textContent = togglerText;
			} else {
				$filtersSelects.classList.remove(CLASSNAMES.filtersSelectsOpenState);
				const togglerText = $filtersToggler.dataset.closeState;
				$filtersTogglerText.textContent = togglerText;
			}
		};

		$categoryBtn.addEventListener('click', (e) => toggleFilter(e, $categoryList));
		$showingBtn.addEventListener('click', (e) => toggleFilter(e, $showingList));
		$filtersToggler.addEventListener('click', toggleSelects);
	});
};

export default filters;
