import Accordion from 'components/accordion';
import { exist } from '../utils/index';

const faq = () => {
	const SELECTORS = {
		section: '.js-faq',
		trigger: '.js-faq-trigger',
	};

	const CLASSNAMES = {
		itemActiveState: 'faq_item--active_state',
	};

	const $sections = document.querySelectorAll(SELECTORS.section);
	if (!exist($sections)) return;

	$sections.forEach(($section) => {
		const accordion = Accordion({
			triggers: $section.querySelectorAll(SELECTORS.trigger),
			activeStateName: CLASSNAMES.itemActiveState,
		});
	});
};

export default faq;
