import { exist } from '../utils/index';

const timer = () => {
	const SELECTORS = {
		section: '.js-timer-wrapper',
	};

	const ATTRIBUTES = {
		trigger: 'data-timer-trigger',
		currentTime: 'data-current-time',
	};

	const $sections = document.querySelectorAll(SELECTORS.section);

	if (!exist($sections)) return;

	$sections.forEach(($section) => {
		const $triggers = $section.querySelectorAll(`[${ATTRIBUTES.trigger}]`);

		if (!exist($triggers)) return;

		const { localStorage } = window;
		const firstVisitStartHours = 18;
		const firstVisitStartMinutes = 44;
		const firstVisitStartSeconds = 11;
		let firstVisitTimestamp;
		let startTime;
		let remainingTime;

		const setZero = (num) => (num < 10 ? `0${num}` : num);

		const getLocalStorageTime = (index) => {
			const now = new Date().getTime();
			let firstVisitData = localStorage.getItem(`firstVisitData${index}`);

			if (firstVisitData) {
				firstVisitData = JSON.parse(firstVisitData);
				firstVisitTimestamp = firstVisitData.firstVisitTimestamp;
				startTime = firstVisitData.startTime;
			} else {
				firstVisitTimestamp = now;
				startTime =
					now +
					firstVisitStartHours * 60 * 60 * 1000 +
					firstVisitStartMinutes * 60 * 1000 +
					firstVisitStartSeconds * 1000;
				localStorage.setItem(`firstVisitData${index}`, JSON.stringify({ firstVisitTimestamp, startTime }));
			}

			if (now < startTime) {
				remainingTime = startTime - now;
			} else {
				const milliseconds = 24 * 60 * 60 * 1000;

				remainingTime = milliseconds - ((now - startTime) % milliseconds);
				startTime = now + remainingTime;
				localStorage.setItem(`firstVisitData${index}`, JSON.stringify({ firstVisitTimestamp, startTime }));
			}
		};

		const updateClock = ($target, index) => {
			getLocalStorageTime(index);

			const hours = setZero(Math.floor(remainingTime / (1000 * 60 * 60)));
			const minutes = setZero(Math.floor((remainingTime % (1000 * 60 * 60)) / (1000 * 60)));
			const seconds = setZero(Math.floor((remainingTime % (1000 * 60)) / 1000));
			const formattedDate = `${hours}:${minutes}:${seconds}`;

			$target.setAttribute(ATTRIBUTES.currentTime, formattedDate);

			setTimeout(() => updateClock($target, index), 1000);
		};

		$triggers.forEach(($trigger, index) => updateClock($trigger, index));
	});
};

export default timer;
