/* eslint-disable indent */
/* eslint-disable default-case */
import { exist, onWindowResize } from '../utils/index';

const setTime = ($container, value) => {
	const $containerEl = $container;
	$containerEl.textContent = value.toString().padStart(2, '0');
};

const getOrdinalSuffix = (day) => {
	if (![11, 12, 13].includes(day % 100)) {
		switch (day % 10) {
			case 1:
				return `${day}st`;
			case 2:
				return `${day}nd`;
			case 3:
				return `${day}rd`;
		}
	}
	return `${day}th`;
};

const formatEndDate = (date) => {
	const day = date.getDate();
	const month = date.toLocaleString('en-US', { month: 'long' });
	return `${getOrdinalSuffix(day)} ${month.charAt(0).toUpperCase() + month.slice(1)}`;
};

const timerBanner = () => {
	const SELECTORS = {
		element: '.js-timer-banner',
		days: '.js-timer-days',
		hours: '.js-timer-hours',
		minutes: '.js-timer-minutes',
		seconds: '.js-timer-seconds',
		dateContainer: '.js-day',
	};

	const $banner = document.querySelector(SELECTORS.element);
	if (!exist($banner)) return;
	const { start, end } = $banner.dataset;

	const $days = $banner.querySelector(SELECTORS.days);
	const $hours = $banner.querySelector(SELECTORS.hours);
	const $minutes = $banner.querySelector(SELECTORS.minutes);
	const $seconds = $banner.querySelector(SELECTORS.seconds);

	const $dateContainer = $banner.querySelector(SELECTORS.dateContainer);

	if (
		exist($days) &&
		exist($hours) &&
		exist($minutes) &&
		exist($seconds) &&
		exist($dateContainer) &&
		start &&
		end
	) {
		let startTime = new Date(start).getTime();
		let endTime = new Date(end).getTime();
		const period = endTime - startTime;

		const updateEndDate = () => {
			const endDate = new Date(endTime);
			$dateContainer.textContent = formatEndDate(endDate);
		};

		const updateTimer = () => {
			const now = new Date().getTime();
			const timeElapsed = now - startTime;
			const timeRemaining = period - (timeElapsed % period);

			if (timeElapsed >= period) {
				startTime = now - (timeElapsed % period);
				endTime = startTime + period;
				updateEndDate();
			}

			const days = Math.floor(timeRemaining / (1000 * 60 * 60 * 24));
			const hours = Math.floor((timeRemaining % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
			const minutes = Math.floor((timeRemaining % (1000 * 60 * 60)) / (1000 * 60));
			const seconds = Math.floor((timeRemaining % (1000 * 60)) / 1000);

			setTime($days, days);
			setTime($hours, hours);
			setTime($minutes, minutes);
			setTime($seconds, seconds);
		};

		updateEndDate();
		updateTimer();
		setInterval(updateTimer, 1000);
	}

	const barOffsetsHandler = () => {
		const offset = $banner.clientHeight;
		document.documentElement.style.setProperty('--height-timer-banner', `${offset}px`);
	};

	onWindowResize(barOffsetsHandler);
};

export default timerBanner;
