import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import { exist, onWindowResize } from '../utils';

gsap.registerPlugin(ScrollTrigger);

const initProgressBarAnimation = ($fill, $parent) => {
	return gsap.fromTo(
		$fill,
		{
			translateX: '-100%',
		},
		{
			translateX: 0,
			ease: 'none',
			scrollTrigger: {
				trigger: $parent,
				scrub: true,
				start: 'top top',
				end: 'bottom bottom',
			},
		},
	);
};

const progressBar = () => {
	const SELECTORS = {
		bar: '.js-progress-bar',
		fill: '.js-progress-bar-fill',
		wrapper: '.js-wrapper',
	};

	const $wrapper = document.querySelector(SELECTORS.wrapper);
	const $bar = document.querySelector(SELECTORS.bar);
	const $fill = $bar?.querySelector(SELECTORS.fill);

	if (!exist($fill) || !exist($bar) || !exist($wrapper)) return;

	let wrapperPrevHeight = $wrapper.clientHeight;
	let isChecking = false;

	const progressScrolltrigger = initProgressBarAnimation($fill, $wrapper);

	const refreshProgressBar = () => {
		const wrapperHeight = $wrapper.clientHeight;

		if (wrapperHeight === wrapperPrevHeight) return;

		wrapperPrevHeight = wrapperHeight;
		progressScrolltrigger?.scrollTrigger?.refresh();
	};

	onWindowResize(refreshProgressBar);

	// eslint-disable-next-line no-undef
	const observer = new MutationObserver(() => {
		if (isChecking) return;

		isChecking = true;

		setTimeout(() => {
			refreshProgressBar();
			isChecking = false;
		}, 500);
	});

	observer.observe($wrapper, {
		attributes: true,
		attributeFilter: ['style'],
		subtree: true,
	});
};

export default progressBar;
