import { exist, onWindowResize, getWindowSize, BREAKPOINTS } from '../utils/index';

const testimonials = () => {
	const SELECTORS = {
		testimonials: '.js-stars-wrap',
		testimonialsList: '.js-testimonials-list',
		testimonialsBtn: '.js-testimonials-btn',
		stars: '.js-stars',
		filledStar: '.js-star-fill',
	};

	const CLASSNAMES = {
		listOpenState: 'testimonials__list_w--open_state',
	};

	const BTN_TEXT = {
		open: 'See all testimonials',
		close: 'See less testimonials',
	};

	const initRating = ($stars) => {
		const $filledStars = $stars.querySelectorAll(SELECTORS.filledStar);
		if (!exist($filledStars)) return;

		const { rating } = $stars.dataset;
		const ratingValue = parseFloat(rating);

		const wholePart = Math.floor(ratingValue);
		const fractionPart = Math.round((ratingValue % 1) * 100) / 100;

		$filledStars.forEach(($star, index) => {
			const $starEl = $star;

			if (index === wholePart) {
				$starEl.style.clipPath = `inset(0 ${100 - fractionPart * 100}% 0 0)`;
			} else if (index > wholePart) {
				$starEl.style.opacity = 0;
			}
		});
	};

	const $sections = document.querySelectorAll(SELECTORS.testimonials);
	if (!exist($sections)) return;

	let height;

	$sections.forEach(($section) => {
		const $testimonialsList = $section.querySelector(SELECTORS.testimonialsList);
		const $testimonialsBtn = $section.querySelector(SELECTORS.testimonialsBtn);
		const $stars = $section.querySelectorAll(SELECTORS.stars);

		const initialHeight = $section.dataset.height;

		if (exist($stars)) {
			$stars.forEach(($el) => {
				initRating($el);
			});
		}

		if (!exist($testimonialsList) || !exist($testimonialsBtn)) return;

		let isListOpen = false;

		const openAllTestimonials = () => {
			$testimonialsList.classList.add(CLASSNAMES.listOpenState);
			$testimonialsList.style.maxHeight = `${$testimonialsList.scrollHeight}px`;
			$testimonialsBtn.innerText = BTN_TEXT.close;
			isListOpen = true;
		};

		const closeAllTestimonials = () => {
			$testimonialsList.classList.remove(CLASSNAMES.listOpenState);
			$testimonialsList.style.maxHeight = height;
			$testimonialsBtn.innerText = BTN_TEXT.open;
			isListOpen = false;
		};

		const onBtnClick = () => {
			if (isListOpen) {
				closeAllTestimonials();
			} else {
				openAllTestimonials();
			}
		};

		$testimonialsBtn.addEventListener('click', onBtnClick);

		let lastWindowWidth;

		onWindowResize(() => {
			const { windowWidth } = getWindowSize();

			height = windowWidth < BREAKPOINTS.mediaPoint2 ? '1100px' : `${initialHeight}px`;

			if (lastWindowWidth !== windowWidth) {
				closeAllTestimonials();
			}

			lastWindowWidth = windowWidth;
		});
	});
};

export default testimonials;
